const setTitle = (meta, next) => {
  meta && (document.title = meta.title || '')
  next()
}

router.beforeEach(async (to, from, next) => {
  let { meta, name } = to
  if (name === 'Login' || name === 'ForgetPwd') return setTitle(meta, next)
  let token = store.getters.s_token
  let { defaultPath, menuAuthorityUrls } = store.state
  if (token) { // 
    if (!defaultPath) { // 
      defaultPath = await store.dispatch('getMenus')
      menuAuthorityUrls = store.state.menuAuthorityUrls
    }
    if (meta.menuActive) {
      let [tabMenuName, sideName] = meta.menuActive
      let hasTab = ~menuAuthorityUrls.indexOf(tabMenuName)
      let hasSide = ~menuAuthorityUrls.indexOf(sideName)

      // 
      if (!(hasTab && hasSide) && name !== 'Layout') {
        next({ name: defaultPath })
        location.reload()
        return
      }
      store.commit('SET_TAB_MENU_NAME', tabMenuName)
      store.commit('SET_SIDE_NAME', sideName)
    }
    return name === 'Layout' ? next({ name: defaultPath }) : setTitle(meta, next)
  }
  next({ name: 'Login' })
})

export default router
