export function isArray(val) {
  return Object.prototype.toString.call(val) === '[object Array]'
}

export function isObject(val) {
  return Object.prototype.toString.call(val) === '[object Object]'
}

export function isEmptyObj(obj) {
  return Object.keys(obj).length === 0
}

export const regPhone = /^((0\d{2,3}-?\d{7,8})|(1[356789]\d{9}))$/
export const regIdNo = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}$)/

// url
export function concatUploadFileUrl(url) {
  url = url[0] === '/' ? url.slice(1) : url
  return `http://bj-ihealthcare.com:52082/${url}`
}

// 
export function throttle({ method, time = 800, context, params } = {}) {
  method.timer && clearTimeout(method.timer)
  method.timer = setTimeout(() => {
    method.call(context || this, params)
  }, time)
}

// 0
export function addZero(n, l = 2, isEnd) {
  var len = String(n).length
  while (len < l) {
    n = isEnd ? `${n}0` : `0${n}`
    len++
  }
  return n
}

// 
export function formatDate(date, format = 'Y-M-D') {
  if (!date) return
  var d = new Date(date)
    , Y = d.getFullYear()
    , M = addZero(d.getMonth() + 1)
    , D = addZero(d.getDate())
    , h = addZero(d.getHours())
    , m = addZero(d.getMinutes())
    , s = addZero(d.getSeconds())
    , o = { Y, M, D, h, m, s }
  return format.replace(/Y|M|D|h|m|s/g, k => {
    return o[k]
  })
}

// 
export function trimStr(data, ret = {}) {
  if (typeof data === 'string') return data.trim()
  if (!data || typeof data !== 'object') return data
  Object.keys(data).forEach(key => {
    let dataType = typeof data[key]
    if (dataType === 'object') {
      ret[key] = isArray(data[key]) ? [] : {}
      return trimStr(data[key], ret[key])
    }
    if (dataType === 'string') return ret[key] = data[key].trim()
    ret[key] = data[key]
  })
  return ret
}

export function filterEmptyField(data) {
  let ret = Object.create(null)
  if (typeof data === 'string' || isArray(data)) return data
  if (isEmptyObj(data)) return ret
  let trimData = trimStr(data)
  Object.keys(data).forEach(key => {
    if (data[key] !== '') {
      ret[key] = trimData[key]
    }
  })
  return ret
}

// 
export function outInt(val, key, data) {
  let value, isInt = /^\d+$/.test(val),
    oldVal = data[key]
  value = val === '' ? '' :
    isInt ? (val.length > 1 && val[0] === '0') ?
      val.slice(1) : val
      : oldVal
  isArray(data) ? data.splice(key, 1, value) : (data[key] = value)
  return value
}

// 
export function outNum(val, key, data) {
  let value, isNumber = /^\d+(\.|(\.\d+)?)$/.test(val),
    oldVal = data[key]
  if (val === '') { value = '' }
  else if (isNumber) {
    let isPoint = val.indexOf('.')
    if (~isPoint) {
      // 0
      let zeroBefore = val.slice(0, isPoint)
      if (zeroBefore.indexOf('00') === 0)
        value = val.slice(1)
      else value = val
    } else
      value = (val.length > 1 && val[0] === '0') ? val.slice(1) : val
  } else value = oldVal
  isArray(data) ? data.splice(key, 1, value) : (data[key] = value)
  return value
}

// 
export function outNegative(val, key, data) {
  let value, oldVal = data[key], last = val.slice(-1)
  if (val === '') { value = '' }
  else if (val === '-') value = '-'
  else if (!isNaN(Number(val))) {
    if ('.' === last) {
      value = val === '-0.' ? ('-' + Number(val) + last) : +val + last
    }
    else if ('0' === last && ~val.indexOf('.')) value = val
    else {
      value = val === '-0' ? ('-' + Number(val)) : +val + ''
    }
  } else value = oldVal
  isArray(data) ? data.splice(key, 1, value) : (data[key] = value)
  return value
}

export function encryptedPwd(pwd) {
  let ret = ''
  for (let i = pwd.length; i--;) {
    ret += String.fromCharCode(pwd[i].charCodeAt() + 2019)
  }
  return ret
}

export function decryptPwd(pwd) {
  let ret = ''
  for (let i = pwd.length; i--;) {
    ret += String.fromCharCode(pwd[i].charCodeAt() - 2019)
  }
  return ret
}

// 
export function lenRule(len = 50) {
  return [
    { max: len, message: `${len}` },
    { max: len, message: `${len}`, trigger: 'blur' }
  ]
}

// 
export function handleMenu(menus) {
  let tabMenus = [],
    sideMenus = {},
    btnAuthorityIds = [],
    menuAuthorityUrls = [],
    defaultPath = menus[0].children[0].menuUrl // path
  tabMenus = menus.map(item => {
    sideMenus[item.menuUrl] = item.children.map(items => {
      return {
        label: items.menuName,
        name: items.menuUrl,
        id: items.menuId,
        icon: items.iconClass
      }
    })
    let it = { name: item.menuUrl, label: item.menuName, id: item.menuId }
    if (item.children.length > 1) {
      let redirect = item.children[0].menuUrl
      return { ...it, redirect }
    }
    return it
  })

    ; (function handleFn(list) {
      list.forEach(item => {
        if (item.menuType == 2) {
          btnAuthorityIds.push(item.menuId)
        }
        if (item.menuType == 1 && menuAuthorityUrls.indexOf(item.menuUrl) === -1) {
          menuAuthorityUrls.push(item.menuUrl)
        }
        if (item.children && item.children.length) {
          handleFn(item.children)
        }
      })
    })(menus)

  return {
    tabMenus,
    sideMenus,
    btnAuthorityIds,
    menuAuthorityUrls,
    defaultPath
  }
}

// 
const types = [
  { type: 1, name: '' },
  { type: 2, name: '' },
  { type: 4, name: '' },
  { type: 8, name: '' },
  { type: 16, name: '' },
  { type: 32, name: '' },
  { type: 64, name: '2' },
  { type: 128, name: '' }
]
// 
export function calcType(val) {
  if (val === '0' || !val) return Object.create(null)
  let typeList = []
  let nameList = []
  types.forEach(item => {
    if (val & item.type) {
      typeList.push(item.type)
      nameList.push(item.name)
    }
  })
  return { typeList, nameList }
}

/**
 * @name: 
 * @param {Stream} data 
 * @param {String} fileName
 * @param {String} fileType
 * @return: void
 */
export function exportFile(data, fileName, fileType = 'xlsx') {
  let blob = new Blob([data], { type: 'application/vnd.ms-excel;charset=utf-8' })
  let objectUrl = URL.createObjectURL(blob)
  let _fileName = `${fileName}-${formatDate(Date.now(), 'YMD')}.${fileType}`

  // IE
  if (~navigator.userAgent.indexOf('MSIE')) {
    navigator.msSaveBlob(blob, _fileName)
    return
  }
  let a = document.createElement('a')
  document.body.appendChild(a)
  a.setAttribute('style', 'display:none')
  a.setAttribute('href', objectUrl)
  a.setAttribute('download', _fileName)
  a.click()
  document.body.removeChild(a)
  URL.revokeObjectURL(objectUrl)
}




