import { formatDate } from '@/lib/utils'

export default {
  // 
  formatDate(date, format = 'Y-M-D') {
    return date ? formatDate(date, format) : '-'
  },

  // 
  emptyData(val, text = '-') {
    return (val || val === 0) ? val : text
  }
}
