import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import getters from './getters'
import mutations from './mutations'
import actions from './actions'
import sesstionData from './module/sesstion-data'
import localData from './module/local-data'

Vue.use(Vuex)

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: {
    sesstionData,
    localData
  }
})

// 
window.addEventListener('beforeunload', e => {
  // vuex
  sessionStorage.setItem('sesstionVuex', JSON.stringify(sesstionData.state))
  localStorage.setItem('localVuex', JSON.stringify(localData.state))
})

