import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

// 
import './styles/index.less'

// 
import BjPagintion from './components/page-list/Pagination.vue'
Vue.component('bj-pagination', BjPagintion)

// 
import filters from './filters'
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

// Vue
import { xhrGet, xhrPost } from '@/lib/xhr.js'
import { trimStr, outInt, outNum, outNegative, filterEmptyField } from '@/lib/utils'
Object.assign(Vue.prototype, {
  '$get': xhrGet,
  '$post': xhrPost,
  '$trimStr': trimStr,
  '$filterEmptyField': filterEmptyField,
  '$outInt': outInt,
  '$outNum': outNum,
  '$outNegative': outNegative
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
