import axios from 'axios'
import qs from 'qs'
import store from '../store'
import { filterEmptyField } from '@/lib/utils'

// 'http://bj-ihealthcare.com:52087/phy-test' :
// 'http://bj-ihealthcare.com:52087/phy-dev'

const baseURL = window.__BASE_URL__
  ? window.__BASE_URL__
  : process.env.NODE_ENV === 'production'
    ? 'http://bj-ihealthcare.com:52081/phy-test'
    : 'http://192.168.10.120:8888/phy-dev'

const xhr = axios.create({
  baseURL
})

// 
xhr.interceptors.request.use(config => {
  let { isJson } = config

  // postdata, getparams
  let data = filterEmptyField(config.data)
  // config.params = filterEmptyField(config.params)
  config.data = isJson ? data : qs.stringify(data)
  return config;
}, error => Promise.reject(error))

// 
xhr.interceptors.response.use(response => {
  if (response.data.code === 401) {
    if (store.getters.s_token) {
      store.dispatch('logout')
    } else {
      return { data: { code: 401, msg: false } }
    }
  }
  return response
}, error => {
  return Promise.reject(error)
})

export const xhrGet = (url, params = {}) => new Promise((resolve, reject) => {
  xhr.get(url, {
    params,
    headers: {
      token: store.getters.s_token
    }
  }).then(({ data }) => {
    data.code === 200 ? resolve(data) : reject(data.msg)
  }).catch(err => reject(''))
})

export const xhrPost = (url, params = {}, ctrlStr = '') => new Promise((resolve, reject) => {
  let isJson = ~ctrlStr.indexOf('json')
  let noToken = ~ctrlStr.indexOf('noToken')
  let headers = {
    'Content-Type': isJson ? 'application/json;charset=UTF-8' : 'application/x-www-form-urlencoded',
    token: store.getters.s_token
  }
  if (noToken) delete headers.token
  xhr.post(url, params, { headers, isJson }).then(({ data }) => {
    data.code === 200 ? resolve(data) : reject(data.msg)
  }).catch(err => reject(''))
})

export const xhrDownload = (url, params = {}, type = 'json') => new Promise((resolve, reject) => {
  const headers = {
    token: store.getters.s_token,
  }
  xhr.post(url, params, { headers, responseType: type }).then(res => {
    resolve(res)
  }).catch(err => reject(''))
})

export default xhr