<!--
 * @Descripttion: tab
 * @Author: 
 * @Date: 2019-05-27 13:58:18
 * @LastEditors: 
 * @LastEditTime: 2019-08-12 13:07:44
 * @example: <bj-header />
 -->

<template>
  <div class="el-header header-wrapper">
    <div class="header-info flex-justify flex-v-center">
      <div class="logo">
        <span><img src="@/assets/img/logo.png"></span>
        
      </div>
      <el-dropdown class="hand-shape"
        trigger="click"
        @command='handleCommand'>
        <div class="flex-v-center">
          <img src="@/assets/img/head.png"
            alt="">
          <div class="text-info">
            <p>{{o_userinfo.orgName}}</p>
            <p>{{o_userinfo.realName}}</p>
          </div>
          <i class="el-icon-arrow-down"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command='updatePwd'></el-dropdown-item>
          <el-dropdown-item command='quit'></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="head-menu flex">
      <div v-for="(item, i) in tabMenus"
        :key='item.name'
        @click="tiggerTab(item)"
        :style="{ zIndex: tabMenus.length - i }"
        :class="{'active': s_tab_menu_name === item.name}">
        <a class="text">{{item.label}}</a>
      </div>
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      title=""
      :close-on-click-modal='false'
      append-to-body
      center>
      <el-form size="small"
        :model="formFields"
        :rules="rules"
        label-width="auto"
        ref='form'
        style="padding: 20px 30px 0">
        <el-form-item label=""
          prop="passwords">
          <el-input type="password"
            v-model.trim="formFields.passwords"
            placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=""
          prop="newPasswords">
          <el-input type="password"
            v-model.trim="formFields.newPasswords"
            placeholder="8~16//"
            autocomplete="new-password"
            maxlength="17"></el-input>
        </el-form-item>
        <el-form-item label=""
          prop="confirmPwd">
          <el-input type="password"
            v-model.trim="formFields.confirmPwd"
            placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot='footer'>
        <el-button class="btn-hollowing"
          @click="dialogIsShow = false"></el-button>
        <el-button class="btn-info"
          @click="onSubmit"
          :loading="submitLoading"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'bj-header',
  data() {
    return {
      menus: [
        { label: '', name: 'CheckIn' },
        { label: '', name: 'FirstTrial' },
        { label: '', name: 'TotalCheck' },
        { label: '', name: 'PrintReport' },
        { label: '', name: 'ReportForm' },
        { label: '', name: 'SystemManage' }
      ],
      dialogIsShow: false,
      formFields: {
        passwords: '',
        newPasswords: '',
        confirmPwd: ''
      },

      rules: {
        passwords: { required: true, message: '', trigger: 'blur' },
        newPasswords: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator(rule, val, cb) {
              if (/^\d+$/.test(val)) return cb(new Error(''))
              if (/^[a-zA-Z]+$/.test(val)) return cb(new Error(''))
              if (/^[^0-9A-Za-z]+$/.test(val)) return cb(new Error(''))
              return cb()
            },
            trigger: 'blur'
          },
          { min: 8, max: 16, message: '8~16', trigger: 'blur' },
          { max: 16, message: '8~16' }
        ],
        confirmPwd: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: (rule, val, cb) => {
              return this.formFields.newPasswords === val ? cb() : cb(new Error(rule.message))
            },
            message: '',
            trigger: 'blur'
          }
        ]
      },
      submitLoading: false
    }
  },

  methods: {
    tiggerTab({ name, redirect }) {
      if (this.s_tab_menu_name === name) return
      this.$router.push({ name: redirect ? redirect : name })
    },

    handleCommand(command) {
      command === 'quit'
        ? this.$confirm('', {
            title: '',
            showClose: false,
            customClass: 'msg-box'
          })
            .then(e => {
              this.$store.dispatch('logout')
            })
            .catch(cancel => cancel)
        : (this.dialogIsShow = true)
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        let { passwords, newPasswords } = this.formFields
        if (passwords === newPasswords) return this.$message.warning('')
        this.submitLoading = true
        this.$post('/manageUser/updatePasswords', {
          passwords,
          newPasswords
        })
          .then(({ data }) => {
            this.$store.commit('SET_USERINFO', { ...this.o_userinfo, passwords: '' })
            this.dialogIsShow = false

            // 
            let imgUrl = require('@/assets/img/success-1.png')
            this.$alert('', {
              message: `<img src='${imgUrl}' width='220' height='186' />
                <p style='margin-top:20px;color:#999'></p>`,
              dangerouslyUseHTMLString: true,
              showConfirmButton: false,
              showClose: false,
              center: true,
              callback() {}
            })
            setTimeout(() => {
              // 
              this.$msgbox.close()
              this.$store.dispatch('logout')
            }, 3000)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    }
  },
  computed: {
    ...mapState(['tabMenus']),
    ...mapGetters(['o_userinfo', 's_tab_menu_name', 'a_tabMenu'])
  }
}
</script>
<style lang="less">
.header-wrapper {
  position: relative;
  padding: 0 !important;
  .header-info {
    height: 70px;
    .logo {
      height: 70px;
      line-height: 70px;
      font-size: 28px;
      font-weight: bold;
      color: #fff;
      span {
        margin-right: 10px;
        img {
          vertical-align: middle;
        }
      }
    }
  }

  .element-drop {
    .el-dropdown {
      // @include flex-start;
      button {
        background: none !important;
        border: none !important;
      }
      span {
        //  @include flex-start;
        dl {
          display: flex;
          dt {
            width: 38px;
            height: 38px;
            border-radius: 50%;
            margin-right: 8px;
            text-align: left;
          }
          dd {
            font-size: 14px;
            line-height: 20px;
          }
        }
        .el-icon--right {
          font-size: 18px;
          margin-left: 20px;
          font-weight: bold;
        }
      }
    }
  }
}

.head-menu {
  height: 52px;
  & > div {
    position: relative;
    margin-right: 4px;
    width: 175px;
    height: 100%;
    border-radius: 10px 10px 0 0;
    text-align: center;
    font-size: 16px;
    box-shadow: 3px 2px 16px 0px rgba(10, 124, 103, 0.56);
    .text {
      display: block;
      position: relative;
      width: 100%;
      line-height: 52px;
      height: 100%;
      font-weight: 700;
      background: #f2ffff;
      border-radius: 10px 10px 0 0;
      color: #07caa6;
      cursor: pointer;
    }
    &:before {
      content: '';
      position: absolute;
      top: 10px;
      bottom: -2px;
      right: -3px;
      width: 10px;
      background: #f2ffff;
      transform: rotate(-7deg);
      box-shadow: 3px 2px 16px 0px rgba(10, 124, 103, 0.56);
    }
    &:hover,
    &.active {
      &:before {
        background: #ffc000;
      }
      .text {
        background: #ffc000;
        color: #fff;
      }
    }
  }
}

.el-dropdown-menu__item {
  white-space: nowrap;
}

.text-info {
  margin: 0 16px;
  text-align: center;
  color: #fff;
}
.el-icon-arrow-down {
  font-size: 20px;
  color: #fff;
}
</style>





