const state = {
  menuIdx: 0,
  tabMenuName: '',
  sideName: ''
}

// IIFE() 
const sesstionData = (() => {
  let data = JSON.parse(sessionStorage.getItem('sesstionVuex')) || state
  sessionStorage.removeItem('sesstionVuex')
  return data
})()

export default sesstionData