const state = {
  userInfo: {},
  isRemenber: false,
  token: ''
}

// IIFE() 
const localData = (() => {
  let data = JSON.parse(localStorage.getItem('localVuex')) || state
  localStorage.removeItem('localVuex')
  return data
})()

export default localData