<template>
  <div class="aside-nav">
    <el-menu :default-active="s_side_name"
      @select="onSelect">
      <el-menu-item :index="item.name"
        v-for="item in sideMenus[s_tab_menu_name]"
        :key='item.name'>
        <i class="iconfont"
          :class="`bj-${item.icon}`"
          style="display:inline-block;width:36px"></i>
        <span slot="title">{{item.label}}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'

export default {
  name: 'side-menu',
  data() {
    return {
      menuData: {
        // 
        CheckIn: [{ label: '', name: 'CheckIn', index: '1', icon: 'counter' }],
        FirstTrial: [{ label: '', name: 'FirstTrial', index: '2', icon: 'stage' }],
        TotalCheck: [{ label: '', name: 'TotalCheck', index: '3', icon: 'station' }],
        PrintReport: [{ label: '', name: 'PrintReport', index: '4', icon: 'print' }],
        ReportForm: [
          { label: '', name: 'ReportForm', index: '5', icon: 'report' },
          { label: '', name: 'medicalrecord', index: '5', icon: 'medical' },
          { label: '', name: 'trialdoctor', index: '5', icon: 'firstdoc' },
          { label: '', name: 'totalcheckdoctor', index: '5', icon: 'totaldoc' },
          { label: '', name: 'reviewerstatistics', index: '5', icon: 'statistics' },
          { label: '', name: 'Analysisreport', index: '5', icon: 'analysis' }
        ],
        SystemManage: [
          {
            label: '',
            name: 'AgencyList',
            index: '6',
            icon: 'agency'
          },
          {
            label: '',
            name: 'DepartmentList',
            index: '7',
            icon: 'department'
          },
          {
            label: '',
            name: 'RoleList',
            index: '8',
            icon: 'role'
          },
          {
            label: '',
            name: 'UserList',
            index: '9',
            icon: 'user'
          },
          {
            label: '',
            name: 'ProjectList',
            index: '10',
            icon: 'project'
          },
          {
            label: '',
            name: 'BasicDataList',
            index: '11',
            icon: 'basic-data'
          },
          {
            label: '',
            name: 'SetMealList',
            index: '12',
            icon: 'set-meal'
          },
          {
            label: '',
            name: 'CategoryList',
            index: '13',
            icon: 'category'
          },
          {
            label: '',
            name: 'GuideSheet',
            index: '14',
            icon: 'guide-sheet'
          },
          {
            label: '',
            name: 'MedicalReport',
            index: '15',
            icon: 'medical-report'
          },
          {
            label: 'LOGO',
            name: 'LogoManage',
            index: '16',
            icon: 'logo'
          }
        ]
      }
    }
  },
  computed: {
    ...mapState(['sideMenus']),
    ...mapGetters(['n_menu_idx', 's_tab_menu_name', 's_side_name', 'o_sideMenu'])
  },
  methods: {
    onSelect(name) {
      this.$router.push({ name })
    }
  }
}
</script>


<style lang="less" scoped>
.aside-nav {
  .el-menu-item {
    color: #999;
    &:focus {
      background: #fff;
    }
    &.is-active {
      color: #07cba7;
      background: #ecf5ff;
    }
  }
}

.el-menu {
  border: 0 none;
}
</style>
