import xhr from '@/lib/xhr'
import { xhrPost } from '@/lib/xhr'
import router from '../router'
import { Message } from 'element-ui';
import { encryptedPwd, handleMenu } from '@/lib/utils'

export default {
  async login({ dispatch, commit }, { params, isRemenber }) {
    const { data } = await xhr.post('/login/loginByPwd', params)
    if (!data || data.code !== 200) return Message.error(data ? data.msg : '')
    commit('SET_IS_REMENBER', isRemenber)
    let passwords = encryptedPwd(params.passwords)
    commit('SET_USERINFO', { ...params, passwords, realName: data.data.realName })
    commit('SET_TOKEN', data.data.token)
    const name = await dispatch('getMenus')
    router.push({ name })
  },

  async logout({ commit }) {
    commit('SET_TOKEN', '')
    commit('SET_TAB_MENU_NAME', '')
    commit('SET_SIDE_NAME', '')
    commit('SET_TAB_MENUS', [])
    commit('SET_SIDE_MENUS', {})
    commit('SET_BTN_AUTHORITY_IDS', [])
    commit('SET_MENU_AUTHORITY_URLS', [])
    commit('SET_DEFAULT_PATH', '')
    router.push({ name: 'Login' })
  },

  async getMenus({ dispatch, commit }) {
    const { data } = await xhrPost('/role/getLoginRoleMenu').catch(err => Message.error(err))
    if (!data) return 'Login'
    let { tabMenus, sideMenus, btnAuthorityIds, menuAuthorityUrls, defaultPath } = handleMenu(data)
    commit('SET_TAB_MENUS', tabMenus)
    commit('SET_SIDE_MENUS', sideMenus)
    commit('SET_BTN_AUTHORITY_IDS', btnAuthorityIds)
    commit('SET_MENU_AUTHORITY_URLS', menuAuthorityUrls)
    commit('SET_DEFAULT_PATH', defaultPath)
    dispatch('getOrgList') // 
    return defaultPath
  },

  getOrgList({ commit }) {
    xhrPost('/sys/org/selectThisOrgAllChildOrg')
      .then(({ data }) => {
        commit('SET_ORG_LIST', data)
      })
      .catch(err => {
        err && Message.error(err)
      })
  }
}