<!--
 * @Descripttion: 
 * @Author: 
 * @Date: 2019-05-30 16:30:26
 * @LastEditors: 
 * @LastEditTime: 2019-08-09 14:35:53
 * @param: {Object} [queryObj]
 * @param: {String} [queryUrl]
 * @emitEvent: {listResult}
 * @emitEvent: {controlLoading}
 * @example: <bj-pagination :queryUrl='{String}'
              :queryObj='{Object}'
              @listResult='{Function}'
              @controlLoading='{Function}' />
 -->


<template>
  <div class="pagination_box flex-center flex-v-center">
    <el-pagination @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      background
      :current-page="pageNum"
      :page-sizes="pageSizeArray"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next"
      :total="totalPage">
    </el-pagination>
    <div><input type="number"
        v-model.number="jumpPage"
        class="input-page"
        @keyup.enter='handleClickChange' /></div>
    <button class="confirm_btn"
      @click="handleClickChange"></button>
  </div>
</template>

<script>
export default {
  name: 'bj-pagination',
  props: {
    queryObj: Object,
    queryUrl: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      pageNum: 1,
      pageSize: 10,
      jumpPage: 1,
      totalPage: 0,
      pageSizeArray: [10, 20, 30, 40, 50]
    }
  },
  created() {
    this.initPageList()
  },
  methods: {
    handleSizeChange(v) {
      this.pageSize = v
      let maxPage = Math.ceil(this.totalPage / v)

      // 
      if (this.pageNum > maxPage) return
      this.initPageList()
    },
    handleCurrentChange(v) {
      this.pageNum = v
      this.jumpPage = v
      this.initPageList()
    },
    handleClickChange() {
      if (!this.jumpPage) return (this.jumpPage = this.pageNum)
      let maxPage = Math.ceil(this.totalPage / this.pageSize)
      if (this.jumpPage > maxPage) {
        this.jumpPage = maxPage || 1
      }
      this.pageNum = this.jumpPage
      this.initPageList()
    },
    initPageList(pageNum = this.pageNum) {
      this.$emit('controlLoading', true)
      const params = {
        pageNum,
        pageSize: this.pageSize,
        ...this.queryObj
      }
      this.$post(this.queryUrl, params)
        .then(result => {
          this.$emit('controlLoading', false)

          // 
          if (!result.data) return
          let { list, pageNum, pageSize, total } = result.data
          this.$emit('listResult', { list, pageNum, pageSize })
          this.pageNum = pageNum
          this.jumpPage = pageNum
          this.totalPage = total
        })
        .catch(error => {
          this.$emit('controlLoading', false)
          error && this.$message.error(error)
        })
    }
  }
}
</script>

<style lang="less" scoped>
.pagination_box {
  height: 58px;
  width: 100%;
}
.confirm_btn {
  height: 28px;
  width: 50px;
  background: none;
  border: 0;
  background-color: #fff;
  border-radius: 4px;
  border: solid 1px #ceeae5;
  font-family: MicrosoftYaHei;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #466791;
  cursor: pointer;
  margin-left: 8px;
  outline: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
}
.input-page {
  width: 50px;
  height: 26px;
  outline: none;
  margin: 0 5px;
  border-radius: 4px;
  border: solid 1px #ceeae5;
  padding: 0 5px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #466791;
  text-align: center;
  line-height: 26px;
}
/deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #07caa6;
  color: #fff;
}

.el-input__inner {
  border: solid 1px #ceeae5;
}
/deep/ .el-pagination__total {
  color: #333;
}
/deep/ .el-pagination__sizes .el-input .el-input__inner:hover {
  border-color: #07caa6;
}
</style>
