const getters = {
  n_menu_idx: state => state.sesstionData.menuIdx,
  s_tab_menu_name: state => state.sesstionData.tabMenuName,
  s_side_name: state => state.sesstionData.sideName,
  o_userinfo: state => state.localData.userInfo,
  b_isRemenber: state => state.localData.isRemenber,
  s_token: state => state.localData.token,
  a_tabMenu: state => state.routerList.map(item => {
    return { label: item.label, name: item.name }
  }),
  o_sideMenu: state => {
    let ret = {}
    state.routerList.forEach(item => {
      ret[item.name] = item.children
    })
    return ret
  }
}

export default getters